@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Form/RadioGroup";
@use "@app/bancor-components/Element/Form/InputRadio";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/DateTime";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Flex";

.Container {
    @include Padding.set($top: delta, $left: beta);
    @include Margin.set($left: delta, $right: delta);
    grid-column: 2;
    > header {
        #{Text.$selector} {
            @include Font.set($size: iota, $weight: normal, $family: "heading");
            @include Margin.set($bottom: delta);
            word-break: break-word;
        }
    }

    #{Form.$selector} {
        #{RadioGroup.$selector} {
            > div {
                @include Flex.set($align: center);

                #{InputRadio.$selector} {
                    @include Margin.set($right: alpha);
                }
                svg {
                    @include Flex.set($shrink: none);
                }
            }
        }

        footer {
            @include Margin.set($top: zeta);
            @include Display.set($display: grid);
            grid-template-columns: 300px 300px;
            align-items: center;
            justify-content: center;
            gap: Layout.get-space(beta);
        }
    }

    .footer {
        @include Flex.set($dir: column);
        @include Padding.set($right: alpha, $left: alpha);
        width: Layout.get-max-width(alpha);
        #{Button.$selector} {
            @include Margin.set($bottom: alpha);
        }
    }
}

.detail {
    @include Margin.set($top: delta, $left: beta, $right: beta);
    @include Flex.set($justify: center);
    @include Margin.set($left: delta, $right: delta);
    grid-column: 2;
    section > ul {
        li {
            @include Flex.set($justify: between);
            overflow: hidden;
            text-overflow: ellipsis;

            > #{Text.$selector}:last-child,
            #{DateTime.$selector},
            #{AmountFormatter.$selector},
            #{AmountFormatter.$selector} > span {
                @include Font.set($weight: bold);
            }
            
            h3 {
                @include Margin.set($top: beta, $bottom: beta);
                @include Font.set($size: delta, $weight: normal, $family: "heading");
            }

            section {
                data {
                    @include Color.set($fg: "gray-light");
                }
            }
        }
    }
}

.filePaymentError {
    .fileErrorTable {
        td:nth-child(8) {
            white-space: normal;
        }

        .dateColumn {
            @include Color.set($fg: "gray-light");
        }
    }

    #{Button.$selector} {
        #{Text.$selector} {
            @include Font.set($deco: none);
            @include Padding.set($left: beta, $right: beta);
        }
    }

    .alert {
        #{Text.$selector},
        #{Button.$selector} {
            @include Display.set($display: inline);
        }

        #{Button.$selector} {
            border: none;
            background-color: inherit;
            cursor: pointer;

            #{Text.$selector} {
                @include Color.set($fg: "green-700");
                @include Padding.set($left: none, $right: none);
            }
        }
    }
}

.fileConfirmation {
    @include Padding.set($top: delta, $left: beta);
    @include Margin.set($left: delta, $right: delta);
    grid-column: 2;
    
    > header {
        #{Text.$selector} {
            @include Font.set($size: iota, $weight: normal, $family: "heading");
            @include Margin.set($bottom: delta);
            word-break: break-word;
        }
    }

    .fileConfirmationSection {
        @include Font.set($weight: medium);
        @include Padding.set($top: beta);
    
        .fileConfirmationTitle {
            @include Display.set($display: flex);
            justify-content: center;
        }
        
        > ul {
            width: 100%;
            @include Padding.set($top: delta, $bottom: delta, $left: zeta, $right: zeta);
            li {
                @include Flex.set($justify: between);
                @include Margin.set($top: alpha, $bottom: alpha);
            }
            .payments {
                @include Padding.set($top: delta, $bottom: delta);
                .value {
                    @include Font.set($weight: black);
                }
                .separator {
                    @include Border.set($color: black);
                }
            }
        }
    }

    footer {
        @include Flex.set($justify: center);
        @include Margin.set($bottom: delta);
    }
}

.generatePaymentsTicket {
    ul {
        li {
            @include Flex.set($justify: space-between);
        }
    }
}

.validateModalFooter {
    @include Flex.set($dir: row);
    gap: Layout.get-space(beta);
}